/* Staff Management Styles */

.staff-page {
  padding: 24px;
  max-width: 1400px;
  margin: 0 auto;
}

/* Page Header */
.page-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 32px;
  padding: 24px 0;
  border-bottom: 1px solid var(--border-color);
}

.page-title-section {
  flex: 1;
}

.page-title {
  font-size: 32px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 8px;
  background: linear-gradient(135deg, var(--primary-color), var(--primary-hover));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.page-description {
  font-size: 16px;
  color: var(--text-secondary);
  margin: 0;
}

.page-actions {
  display: flex;
  gap: 12px;
  align-items: center;
}

/* Staff Controls */
.staff-controls {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 32px;
  padding: 24px;
  background-color: var(--surface-color);
  border-radius: 16px;
  box-shadow: 0 4px 20px var(--shadow-color);
  border: 1px solid var(--border-color);
}

.filters-section {
  display: flex;
  gap: 24px;
  align-items: flex-end;
  flex: 1;
}

.filter-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
  min-width: 200px;
}

.filter-group label {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
}

.filter-input,
.filter-select {
  padding: 12px 16px;
  border: 2px solid var(--border-color);
  border-radius: 12px;
  background-color: var(--surface-color);
  color: var(--text-primary);
  font-size: 14px;
  transition: all var(--transition-fast);
}

.filter-input:focus,
.filter-select:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}

.view-controls {
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: flex-end;
}

.view-toggle {
  display: flex;
  gap: 4px;
  background-color: var(--background-color);
  border-radius: 12px;
  padding: 4px;
  border: 1px solid var(--border-color);
}

.view-btn {
  padding: 8px 16px;
  border: none;
  background: none;
  color: var(--text-secondary);
  cursor: pointer;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  transition: all var(--transition-fast);
}

.view-btn.active {
  background-color: var(--primary-color);
  color: white;
  box-shadow: 0 2px 8px rgba(37, 99, 235, 0.3);
}

.view-btn:hover:not(.active) {
  background-color: var(--background-color);
  color: var(--text-primary);
}

.results-count {
  font-size: 14px;
  color: var(--text-muted);
  font-weight: 500;
}

/* Staff Grid */
.staff-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 24px;
}

.staff-card {
  background-color: var(--surface-color);
  border-radius: 20px;
  box-shadow: 0 8px 32px var(--shadow-color);
  border: 1px solid var(--border-color);
  transition: all var(--transition-normal);
  overflow: hidden;
  position: relative;
  transform-style: preserve-3d;
  animation: fadeInUp 0.6s ease forwards;
  opacity: 0;
  transform: translateY(20px);
}

.staff-card:hover {
  box-shadow: 0 16px 48px var(--shadow-elevated);
  transform: translateY(-8px) rotateX(5deg) rotateY(2deg);
}

.staff-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(135deg, var(--primary-color), var(--primary-hover));
  opacity: 0;
  transition: opacity var(--transition-normal);
}

.staff-card:hover::before {
  opacity: 1;
}

.staff-card::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), transparent);
  opacity: 0;
  transition: opacity var(--transition-normal);
  pointer-events: none;
}

.staff-card:hover::after {
  opacity: 1;
}

.staff-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 24px 16px;
}

.staff-avatar {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  overflow: hidden;
  position: relative;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
}

.staff-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.avatar-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--primary-color), var(--primary-hover));
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 24px;
  font-weight: 700;
}

.staff-card-content {
  padding: 0 24px 24px;
}

.staff-name {
  font-size: 20px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 12px;
  line-height: 1.2;
}

.staff-role {
  margin-bottom: 16px;
}

.role-badge {
  display: inline-block;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  color: white;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.staff-contact {
  margin-bottom: 16px;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 8px;
}

.contact-icon {
  font-size: 16px;
  width: 20px;
  text-align: center;
}

.contact-text {
  font-size: 14px;
  color: var(--text-secondary);
  word-break: break-all;
}

.staff-meta {
  margin-bottom: 20px;
}

.meta-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6px;
}

.meta-label {
  font-size: 12px;
  color: var(--text-muted);
  font-weight: 500;
}

.meta-value {
  font-size: 12px;
  color: var(--text-primary);
  font-weight: 600;
}

.staff-card-actions {
  display: flex;
  gap: 8px;
  padding: 0 24px 24px;
  flex-wrap: wrap;
}

.btn-xs {
  padding: 6px 12px;
  font-size: 12px;
}

.btn-sm {
  padding: 8px 16px;
  font-size: 13px;
}

.btn-danger {
  background: linear-gradient(135deg, #ef4444, #dc2626);
  color: white;
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.3);
}

.btn-danger:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(239, 68, 68, 0.4);
}

/* Status Badges */
.status-badge {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: all var(--transition-fast);
}

.status-badge:hover {
  transform: scale(1.05);
}

.status-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  animation: pulse 2s infinite;
}

.status-dot.online {
  background-color: #10b981;
  box-shadow: 0 0 8px rgba(16, 185, 129, 0.5);
}

.status-dot.offline {
  background-color: #6b7280;
}

.status-online {
  background-color: rgba(16, 185, 129, 0.2);
  color: #10b981;
  border: 1px solid rgba(16, 185, 129, 0.4);
}

.status-offline {
  background-color: rgba(107, 114, 128, 0.2);
  color: #6b7280;
  border: 1px solid rgba(107, 114, 128, 0.4);
}

/* Staff Table */
.staff-table-container {
  background-color: var(--surface-color);
  border-radius: 16px;
  box-shadow: 0 4px 20px var(--shadow-color);
  border: 1px solid var(--border-color);
  overflow: hidden;
}

.staff-table {
  width: 100%;
  border-collapse: collapse;
}

.staff-table th {
  padding: 20px 24px;
  text-align: left;
  font-weight: 600;
  color: var(--text-primary);
  background-color: var(--background-color);
  border-bottom: 2px solid var(--border-color);
  position: sticky;
  top: 0;
  z-index: 10;
  backdrop-filter: blur(10px);
}

.staff-table td {
  padding: 20px 24px;
  border-bottom: 1px solid var(--border-color);
  transition: all var(--transition-normal);
  background-color: var(--surface-color);
}

.staff-table-row {
  transition: all var(--transition-normal);
  transform-style: preserve-3d;
}

.staff-table-row:hover {
  transform: translateX(8px) translateY(-2px) rotateY(2deg);
  box-shadow: 0 8px 25px var(--shadow-elevated);
}

.staff-table-row:hover td {
  background-color: var(--background-color);
  transform: translateZ(10px);
}

.staff-info {
  display: flex;
  align-items: center;
  gap: 16px;
}

.staff-avatar-small {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.staff-avatar-small img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.avatar-placeholder-small {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--primary-color), var(--primary-hover));
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 16px;
  font-weight: 700;
}

.staff-details {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.staff-details .staff-name {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.staff-email {
  font-size: 14px;
  color: var(--text-secondary);
}

.table-actions {
  display: flex;
  gap: 8px;
  align-items: center;
}

/* Loading State */
.staff-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 80px 20px;
  text-align: center;
}

.staff-loading p {
  margin-top: 16px;
  font-size: 16px;
  color: var(--text-secondary);
}

/* Empty State */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 80px 20px;
  text-align: center;
  background-color: var(--surface-color);
  border-radius: 16px;
  border: 1px solid var(--border-color);
}

.empty-icon {
  font-size: 64px;
  margin-bottom: 24px;
  opacity: 0.6;
}

.empty-state h3 {
  font-size: 24px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 12px;
}

.empty-state p {
  font-size: 16px;
  color: var(--text-secondary);
  margin-bottom: 24px;
  max-width: 400px;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .staff-grid {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 20px;
  }
  
  .filters-section {
    gap: 16px;
  }
  
  .filter-group {
    min-width: 160px;
  }
}

@media (max-width: 768px) {
  .staff-page {
    padding: 16px;
  }
  
  .page-header {
    flex-direction: column;
    gap: 16px;
    align-items: flex-start;
  }
  
  .page-title {
    font-size: 24px;
  }
  
  .staff-controls {
    flex-direction: column;
    gap: 20px;
    align-items: stretch;
  }
  
  .filters-section {
    flex-direction: column;
    gap: 16px;
  }
  
  .filter-group {
    min-width: auto;
  }
  
  .view-controls {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  
  .staff-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  
  .staff-card {
    margin: 0 4px;
  }
  
  .staff-table-container {
    overflow-x: auto;
  }
  
  .staff-table {
    min-width: 600px;
  }
  
  .staff-table th,
  .staff-table td {
    padding: 12px 16px;
  }
  
  .table-actions {
    flex-direction: column;
    gap: 4px;
  }
}

@media (max-width: 480px) {
  .page-title {
    font-size: 20px;
  }
  
  .staff-card-header {
    padding: 16px 16px 12px;
  }
  
  .staff-card-content {
    padding: 0 16px 16px;
  }
  
  .staff-card-actions {
    padding: 0 16px 16px;
    flex-direction: column;
  }
  
  .staff-name {
    font-size: 18px;
  }
  
  .staff-avatar {
    width: 48px;
    height: 48px;
  }
  
  .avatar-placeholder {
    font-size: 18px;
  }
  
  .staff-table th,
  .staff-table td {
    padding: 8px 12px;
    font-size: 14px;
  }
  
  .staff-info {
    gap: 12px;
  }
  
  .staff-avatar-small {
    width: 36px;
    height: 36px;
  }
  
  .avatar-placeholder-small {
    font-size: 12px;
  }
}

/* Dark mode enhancements */
[data-theme="dark"] .staff-card {
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

[data-theme="dark"] .staff-card:hover {
  box-shadow: 0 16px 48px rgba(0, 0, 0, 0.4);
}

[data-theme="dark"] .staff-table-container {
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

[data-theme="dark"] .staff-table-row:hover {
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
}

[data-theme="dark"] .staff-controls {
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

/* Animation delays for staggered effect */
.staff-card:nth-child(1) { animation-delay: 0.1s; }
.staff-card:nth-child(2) { animation-delay: 0.2s; }
.staff-card:nth-child(3) { animation-delay: 0.3s; }
.staff-card:nth-child(4) { animation-delay: 0.4s; }
.staff-card:nth-child(5) { animation-delay: 0.5s; }
.staff-card:nth-child(6) { animation-delay: 0.6s; }
.staff-card:nth-child(7) { animation-delay: 0.7s; }
.staff-card:nth-child(8) { animation-delay: 0.8s; }
